grow && fade-out \n  .btn::after = parent\n  .btn:hover::after = child\n  */\n  transform: scaleX(1.4) scaleY(1.6);\n  opacity: 0;\n}\n\n.btn:active, .btn:focus {\n  outline: none;\n  transform: translateY(-0.05rem);\n  box-shadow: 0.5rem 0.5rem 0.5rem rgba(220, 230, 250, 0.6), 0.5rem 0.5rem 0.5rem rgba(255, 215, 0, 0.3);\n}\n\n.btn--white {\n  background-color: #fff;\n  color: rgb(59, 78, 147);\n}\n\n.btn--white::after {\n  background-color: white;\n  color: rgb(84, 87, 37);\n}\n\n.btn--green {\n  background-color: rgb(157, 169, 225);\n  color: #fff;\n}\n\n.btn--green::after {\n  background-color: rgb(157, 169, 225);\n}\n\n.btn::after {\n  /* Must declare content && display properties */\n  content: \"\";\n  display: inline-block;\n  /* pseudo elements are treated as children */\n  height: 100%;\n  width: 100%;\n  border-radius: 10rem; /* 100px */\n  position: absolute;\n  top: 0;\n  left: 0;\n  /* position: absolute need a reference \n  from parent box with position: relative\n  In this case:\n  .btn::after (child) \n  .btn (parent)\n  */\n  z-index: -1;\n  transition: all 0.4s ease;\n  -webkit-transition: all 0.4s ease;\n}\n\n.btn--animated {\n  animation: moveInBottom 0.5s ease-out 0.5s;\n  animation-fill-mode: backwards;\n}\n\n.btn-secondary {\n  animation: moveInBottom 0.5s ease-out 0.55s;\n  animation-fill-mode: backwards;\n  /* \n  Initial State of pseudo element\n  .btn = parent\n  .btn::after = child\n  */\n  /* Only Discover our tour button will have animation */\n}\n\n.btn-secondary, .btn-secondary:link, .btn-secondary:visited {\n  text-transform: uppercase;\n  text-decoration: none;\n  padding: 0.5rem 0.5rem; /* padding: 1.5rem 4rem 15px 40px */\n  display: inline-block;\n  border-radius: 10rem; /* 100px */\n  transition: all 0.1s ease-in;\n  position: relative;\n  font-size: 1rem;\n  border: none;\n  cursor: pointer;\n}\n\n.btn-secondary:hover {\n  transform: scale(1.01) translateY(-0.2rem);\n  box-shadow: 0.5rem 0.5rem 0.5rem rgba(220, 230, 250, 0.6), 0.5rem 0.5rem 0.5rem rgba(255, 215, 0, 0.3);\n  /* box-shadow: offset-x offset-y blur rgba; */\n}\n\n.btn-secondary:hover::after {\n  /* \n  2nd State of pseudo element\n  pseudo element after for .btn:hover state\n  Make pseudo element grow && fade-out \n  .btn::after = parent\n  .btn:hover::after = child\n  */\n  transform: scaleX(1.4) scaleY(1.6);\n  opacity: 0;\n}\n\n.btn-secondary:active, .btn-secondary:focus {\n  outline: none;\n  transform: translateY(-0.1rem);\n  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.3);\n}\n\n.btn-secondary--white {\n  background-color: #fff;\n  color: rgb(59, 78, 147);\n}\n\n.btn-secondary--white::after {\n  background-color: white;\n  color: rgb(84, 87, 37);\n}\n\n.btn-secondary--green {\n  background-color: rgb(157, 169, 225);\n  color: #fff;\n}\n\n.btn-secondary--green::after {\n  background-color: rgb(157, 169, 225);\n}\n\n.btn-secondary::after {\n  /* Must declare content && display properties */\n  content: \"\";\n  display: inline-block;\n  /* pseudo elements are treated as children */\n  height: 100%;\n  width: 100%;\n  border-radius: 10rem; /* 100px */\n  position: absolute;\n  top: 0;\n  left: 0;\n  /* position: absolute need a reference \n  from parent box with position: relative\n  In this case:\n  .btn::after (child) \n  .btn (parent)\n  */\n  z-index: -1;\n  transition: all 0.4s ease;\n  -webkit-transition: all 0.4s ease;\n}\n\n.btn-secondary--animated {\n  animation: moveInBottom 1s ease-out 0.75s;\n  animation-fill-mode: backwards;\n}\n\n.btn-text:link, .btn-text:visited {\n  font-size: 1rem;\n  color: rgb(157, 169, 225);\n  display: inline-block;\n  text-decoration: none;\n  border-bottom: 1px solid rgb(157, 169, 225);\n  padding: 3px;\n  transition: all 0.15s;\n}\n\n.btn-text:hover {\n  transform: translateY(-0.3rem);\n  background-color: rgb(177, 189, 235);\n  color: #fff;\n  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.3);\n  border-radius: 1rem;\n}\n\n.btn-text:active {\n  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.3);\n  transform: translateY(0);\n}\n\n.frosted {\n  background: url();\n  background-size: cover;\n  background-attachment: fixed;\n}\n\n.frosted__children {\n  border-radius: 1.6rem;\n  background: linear-gradient(to bottom right, rgb(242, 242, 251), rgb(232, 237, 246), rgb(212, 217, 226), 0.8);\n  box-shadow: 0.3rem 0.3rem 0.3rem #f2f2fb;\n  border: 1px solid rgba(212, 217, 226, 0.803);\n  position: relative;\n  background: inherit;\n  overflow: hidden;\n  z-index: 1;\n}\n\n.frosted__children:before {\n  background: inherit;\n  content: \"\";\n  position: absolute;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  box-shadow: inset 0 0 2500rem rgba(242, 242, 251, 0.5);\n  z-index: -1;\n}\n\n/*\n0 - 600px:      Phone\n600 - 900px:    Tablet portrait\n900 - 1200px:   Tablet landscape\n[1200 - 1800] is where our normal styles apply\n1800px + :      Big desktop\n\n$breakpoint arguement choices:\n- phone\n- tab-port\n- tab-land\n- big-desktop\n\nORDER: Base + typography > general layout + grid > page layout > components\n\n1em = 16px\n*/\n\n.row {\n  /*\n  If we have enough space, we will have the specified width\n  If we have insufficient space, we will have width: 100%;\n  */\n  margin: 0 auto;\n  /*\n  To apply effects to all columns\n  using attributes selector\n  e.g. \n  [alt=\"Logo\"] {\n  }\n  */\n}\n\n.row:not(:last-child) {\n  margin-bottom: 1rem;\n}\n\n.row::after {\n  content: \"\";\n  display: table;\n  clear: both;\n}\n\n.row [class^=col-] {\n  float: left;\n}\n\n.row [class^=col-]:not(:last-child) {\n  margin-right: 5rem;\n}\n\n.row .col-1-of-2 {\n  width: calc((100% - 5rem) / 2);\n}\n\n.row .col-1-of-3 {\n  width: calc((100% - 2* 5rem) / 3);\n}\n\n.row .col-1-of-4 {\n  width: calc((100% - 3* 5rem) / 4);\n}\n\n.row .col-2-of-3 {\n  width: calc(2*((100% - 2* 5rem)/3) + 5rem);\n}\n\n.row .col-2-of-4 {\n  width: calc(2*((100% - 3* 5rem)/4) + 5rem);\n}\n\n.row .col-3-of-4 {\n  width: calc(3*((100% - 3* 5rem)/4) + 2*5rem);\n}\n\n.row-no {\n  margin: 0 auto;\n  margin-bottom: 0rem;\n}\n\np {\n  background-color: rgb(0, 0, 0);\n}\n\n.buttons__container {\n  display: flex;\n  justify-content: center;\n  margin: 0 auto;\n}\n\n.buttons__container--btn {\n  width: 10%;\n  height: 5vh;\n  font-weight: 600;\n  transition: 0.3s;\n  -webkit-user-select: none;\n     -moz-user-select: none;\n      -ms-user-select: none;\n          user-select: none;\n  touch-action: manipulation;\n  background-color: #f2f2fb;\n  color: #3b4e93;\n  border-radius: 0.5rem;\n  border: 1px solid #b1bdeb;\n  font-size: 1rem;\n  margin-right: 1rem;\n}\n\n.buttons__container--btn:before {\n  content: \"\";\n  position: absolute;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  box-shadow: inset 0 0 2500rem rgba(242, 242, 251, 0.5);\n  filter: blur(1.2rem);\n  z-index: -1;\n}\n\n.buttons__container--btn:hover {\n  transform: translateY(-0.2rem);\n  border-radius: 0.8rem;\n  color: white;\n  background-image: linear-gradient(to bottom right, rgb(177, 189, 235), rgb(157, 169, 225), rgb(137, 149, 205), rgb(157, 169, 225), rgb(177, 189, 235));\n  box-shadow: 0.3rem 0.3rem 0.3rem #f2f2fb;\n  backdrop-filter: blur(1.5rem);\n  -webkit-backdrop-filter: blur(1.5rem);\n  border: 1px solid rgba(212, 217, 226, 0.803);\n  position: relative;\n  overflow: hidden;\n  z-index: 1;\n}\n\n.buttons__container--btn:active {\n  transform: translateY(0rem);\n}";